/* Common css for Desktop*/
.body_container {
  min-height: 500px;
  height: auto;
  display: grid;
  grid-template-columns: minmax(350px,30%) minmax(350px,30%);
  grid-template-rows: repeat(auto-fill,1fr);
  justify-content: center; 
  margin-top: 3%;
}


.leftcontent {
 grid-column: 1 / 2;
 display:flex; 
 align-items:center;
}

.leftcontent>img {
  max-width:80%; 
  position: relative;
  left:10%;
  border-radius: 10px;
  border: solid;
  border-color: slategray;
}
.rightcontent {
  grid-column: 2 / 3;
  display: inline;
}
.rightcontent>div:nth-child(1){
font-size: 40px;
margin: 20% 0;
text-align: left;
border-bottom: solid;
}
.rightcontent>div:nth-child(2){
font-size: 20px;  
line-height: 120%;
}

 .ul {
  font-size: 25px;
  list-style-image: url(../img/img_380775.png);
 }

@media screen and (max-width:780px){
 
  .body_container {
      
      display: inline;

  }
  .leftcontent{
   position: relative;
   margin: 20px auto;
   padding: 0 auto;
  }
       .leftcontent>img {
        max-width:80%; 
        position: relative;
        left:10%;
        border-radius: 10px;
        border: solid;
        border-color: slategray;
       }
       .rightcontent>div:nth-child(1){
       font-size: 40px;
       margin: 5% 0;
       text-align: left;
       border-bottom: solid;
       }
       .rightcontent>div:nth-child(2){
       font-size: 20px;  
       line-height: 140%;
       text-align: justify;
       margin: 10% 3%;
       }
          
  }