
.header_container {
  
    background-image: url(../img/unnamed.jpg) ;
    height: 250px;
    box-sizing:content-box;
    margin:0px;
    padding:0px;
    display: grid;
    column-gap: 3.3%;
    grid-template-columns: 60% 8% 8% 8%;
    grid-template-rows: 40% 40%;
    
}

.header_container>div {
    font-size: 20px;
    font-weight: bold;
    color: white;
   
    grid-row: 2/3;
    text-align: match-parent;
    margin-top:100px;
}
.header_container>div:nth-child(1) {
   font-size: 40px;
   margin-top:80px;
}
.header_container>div:nth-child(2) { 
    
    text-align: right;
}
.header_container>div:nth-child(3) { 
    text-align: center;
    
}
.header_container>div:nth-child(4) {
    text-align: left;
}
.footer_container {

    background-image: url(../img/footer-background-01.jpg) ;
    height: 250px;
}


@media screen and (max-width:780px){


    .header_container>div {
        font-size: 14px;
        font-weight: bold;
        color: white;
       
        grid-row: 2/4;
        text-align: match-parent;
        margin-top:250%;
    }
    .header_container>div:nth-child(1) {
       font-size: 30px;
       margin-top:20%;
    }  
    .header_container {
        height: 200px;
    }
}